<template>
  <v-container v-if="detail.no != -1">
    <v-row>
      <v-col>
        <v-card class="mx-auto" v-if="detail.no">
          <v-card-actions>
            <v-btn icon @click="back">
              <v-icon>mdi-chevron-left</v-icon>
            </v-btn>
          </v-card-actions>
          <v-card-text>
            <div>
              {{ detail.kana }}
              <div class="speak-icon">
                <div class="detail-no">{{ detail.no }}</div>
                <v-btn icon @click="speak('jp')"
                  ><v-icon>mdi-account-voice</v-icon></v-btn
                >
              </div>
            </div>
            <p class="display-1 text--primary">{{ detail.kanji }}</p>
            <div>{{ detail.romaji }}</div>
          </v-card-text>

          <div v-if="detail.meaning_mm">
            <v-card-subtitle class="pb-0 myanmar">
              မြန်မာလိုအဓိပ္ပါယ်
              <div class="speak-icon">
                <v-btn icon @click="speak('mm')"
                  ><v-icon>mdi-account-voice</v-icon></v-btn
                >
              </div>
            </v-card-subtitle>
            <v-card-text class="text--primary myanmar">
              <div>{{ detail.meaning_mm }}</div>
            </v-card-text>
          </div>

          <div v-if="detail.meaning_en">
            <v-card-subtitle class="pb-0">
              English Meaning
              <div class="speak-icon">
                <v-btn icon @click="speak('en')"
                  ><v-icon>mdi-account-voice</v-icon></v-btn
                >
              </div>
            </v-card-subtitle>
            <v-card-text class="text--primary">
              <div>{{ detail.meaning_en }}</div>
            </v-card-text>
          </div>

          <div
            v-if="
              detail.example_sentence_jp.length > 0 &&
              detail.example_sentence_jp[0]
            "
          >
            <div
              v-for="(esj, index) in detail.example_sentence_jp"
              v-bind:key="index"
            >
              <div v-if="index <= 1">
                <ExampleSentenceComponent
                  :index="index"
                  :exampleJp="esj"
                  :exampleEn="detail.example_sentence_en[index]"
                />
              </div>

              <v-expand-transition>
                <div v-show="isShowAllExamples && index > 1">
                  <ExampleSentenceComponent
                    :index="index"
                    :exampleJp="esj"
                    :exampleEn="detail.example_sentence_en[index]"
                  />
                </div>
              </v-expand-transition>
            </div>

            <v-card-actions v-if="detail.example_sentence_jp.length > 2">
              <v-btn icon @click="isShowAllExamples = !isShowAllExamples">
                <v-icon>{{
                  isShowAllExamples ? "mdi-chevron-up" : "mdi-chevron-down"
                }}</v-icon>
              </v-btn>
            </v-card-actions>
          </div>
        </v-card>

        <v-alert
          text
          outlined
          color="deep-orange"
          icon="mdi-cloud-alert"
          class="myanmar"
          v-else
        >
          No kotoba found for the number {{ no }}. <br /><br />
          Kotoba နံပါတ်မှန်အောင် URL မှာပြန်လည်ပြင်ဆင်ပြီး CTRL + R ဖြင့် page
          refresh လုပ်ပေးစေလိုပါသည်။ မှန်ခဲ့သည်ဆိုပါက DB
          တွင်မရှိခြင်းကြောင့်ဖြစ်ပါသည်။
        </v-alert>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import * as CONFIG from "../config";
import ExampleSentenceComponent from "@/components/ExampleSentence.vue";
export default {
  name: "DetailComponent",
  components: {
    ExampleSentenceComponent,
  },
  props: {
    no: String,
  },
  data: () => ({
    detail: {},
    isShowAllExamples: false,
  }),
  created: function () {
    this.displayDetail(this.no);
  },
  methods: {
    displayDetail: async function (no) {
      let that = this;
      console.log("displayDetail", no);
      if (!no) {
        this.detail.no = -1;
        return;
      }
      try {
        that.db = await CONFIG.openDb();
        console.log("openDb DONE", that.db);

        var transaction = that.db.transaction(["dictionaries"], "readonly");
        var objectStore = transaction.objectStore("dictionaries");

        var myIndex = objectStore.index("no");
        var getKeyRequest = myIndex.get(parseInt(no));
        getKeyRequest.onsuccess = function () {
          if (getKeyRequest.result) {
            console.log("success", getKeyRequest.result);
            that.detail = getKeyRequest.result;
            that.detail.example_sentence_jp = that.detail.example_sentence_jp.split(
              "	"
            );
            that.detail.example_sentence_en = that.detail.example_sentence_en.split(
              "	"
            );
          }
        };
      } catch (e) {
        console.log("error", e);
      }
    },
    speak: function (lang) {
      console.log("speak", lang);
      // 'https://thuraucsy.github.io/jlpt_kotoba/tts/$ttsLang/${kotoba.no}.mp3'
      // 'https://thuraucsy.github.io/jlpt_kotoba_jp/tts/jp/${kotoba.no}.mp3'
      let audioUrl = `https://thuraucsy.github.io`;
      if (lang == "jp") {
        audioUrl += `/jlpt_kotoba_jp/tts/jp/${this.detail.no}.mp3`;
      } else {
        audioUrl += `/jlpt_kotoba/tts/${lang}/${this.detail.no}.mp3`;
      }
      new Audio(audioUrl).play();
    },
    back: function () {
      console.log("back");
      window.history.back();
    },
  },
};
</script>

<style>
.v-application p {
  margin-bottom: 0;
}
.speak-icon {
  float: right;
  width: 58px;
  text-align: center;
}
</style>