export const DB_NAME = "jlpt-kotoba-db";
export const DB_VERSION = 1; // Use a long long for this value (don't use a float)
export const DB_STORE_DICTIONARIES = "dictionaries";

export const openDb = function () {
  return new Promise(function (resolve, reject) {
    console.log("openDb ...");
    var req = indexedDB.open(DB_NAME, DB_VERSION);
    req.onsuccess = function () {
      // Equal to: db = req.result;
      resolve(this.result);
    };
    req.onerror = function (evt) {
      console.error("openDb:", evt.target.errorCode);
      reject(evt.target.errorCode);
    };

    req.onupgradeneeded = function (evt) {
      // let db = openRequest.result;
      console.log("openDb.onupgradeneeded", evt.oldVersion);
      switch (evt.oldVersion) { // existing db version
        case 0:
          // version 0 means that the client had no database
          // perform initialization
          var store = evt.currentTarget.result.createObjectStore(DB_STORE_DICTIONARIES, {
            keyPath: "no",
            autoIncrement: true,
          });

          store.createIndex("no", "no", { unique: true });
          store.createIndex("romaji", "romaji", { unique: false });
          store.createIndex("kana", "kana", { unique: false });
          store.createIndex("kanji", "kanji", { unique: false });
          store.createIndex("meaning_mm", "meaning_mm", { unique: false });
          store.createIndex("meaning_en", "meaning_en", { unique: false });
          store.createIndex("is_tran", "is_tran", { unique: false });
          store.createIndex("level", "level", { unique: false });
          store.createIndex("part_of_speech", "part_of_speech", {
            unique: false,
          });
          store.createIndex("example_sentence_jp", "example_sentence_jp", {
            unique: false,
          });
          store.createIndex("example_sentence_en", "example_sentence_en", {
            unique: false,
          });
          break;
        case 1:
          // client had version 1
          // update
          break;
      }
    };
  });
}

export const clearDictionariesStore = function (db) {
  return new Promise(function (resolve, reject) {
    console.log("clearObjectStore");
    var tx = db.transaction(DB_STORE_DICTIONARIES, "readwrite");
    var store = tx.objectStore(DB_STORE_DICTIONARIES);
    var req = store.clear();
    req.onsuccess = function () {
      resolve("success");
    };
    req.onerror = function (evt) {
      console.error("clearObjectStore:", evt.target.errorCode);
      reject(evt.target.errorCode);
    };
  });
}

export const getLevel = function (level, no) {
  if (isNaN(level)) {
    return level;
  }

  level = parseInt(level);
  if (!level && no >= 9539 && no <= 10418) {
    level = 3;
  } else if (level == 1 || level == 2) {
    // nothing to do
  } else if (level == 3 || level == 4) {
    ++level;
  }
  return level;
};