<template>
  <v-container>
    <v-row class="text-center" v-if="isDbEmpty">
      <v-col class="mb-5" cols="12">
        <v-file-input
          show-size
          counter
          accept=".csv"
          label="JLPT kotoba CSV"
          id="fileLoader"
          :loading="loading"
          :disabled="loading"
        ></v-file-input>

        <v-btn
          :loading="loading"
          :disabled="loading"
          class="jlpt-btn"
          rounded
          @click="start"
        >
          IMPORT
          <v-icon right dark> mdi-cloud-upload </v-icon> </v-btn
        ><br />

        <v-alert
          text
          outlined
          color="deep-orange"
          icon="mdi-cloud-alert"
          class="myanmar mt-5"
          v-if="loading"
        >
          ဒေတာများထည့်သွင်းခြင်းလုပ်ငန်းကို စတင်နေပြီဖြစ်ပါသဖြင့် <br />
          Browser tab ကိုဖွင့်ထားလျက် ပြီးဆုံးသည်အထိ <br />
          စောင့်စားပေးပါရန် မေတ္တာရပ်ခံအပ်ပါသည်။ {{ dbInsertCount }}/{{
            csvArr.length
          }}
        </v-alert>
      </v-col>
    </v-row>

    <div v-else>
      <v-row class="text-left">
        <v-col>
          <v-alert
            text
            outlined
            color="deep-orange"
            icon="mdi-cloud-alert"
            class="myanmar"
            v-if="isImportNotCompleted"
          >
            Import လုပ်ငန်းစဥ်မပြီးဆုံးသေးဘဲ အသုံးပြုနေခြင်းဖြစ်ပါသည်။
            <br /><br />
            Import လုပ်ငန်းစဥ်ပြန်လည်စတင်ရန် အကြံပြုပါသည်။ <br />
            Search တွင် import-again ဟုရိုက်၍ Import
            လုပ်ငန်းစဥ်ပြန်လည်စတင်နိုင်ပါသည်။
          </v-alert>
          <v-text-field
            class="search"
            label="Search..."
            hide-details="auto"
            v-on:keyup.enter="search"
            v-on:keyup="searchKeyCheck"
            v-model="searchValue"
            :loading="!isReadingFinish"
            :disabled="!isReadingFinish"
            @click:append="searchClear"
            append-icon="mdi-close"
          >
          </v-text-field>

          <HomeListComponent
            :csvArrFilter="csvArrFilter"
            v-on:childToParent="onChildClick"
            v-if="searchValue"
          ></HomeListComponent>

          <v-row dense v-else-if="isReadingFinish">
            <v-col
              v-for="(listName, index) in kotobaList"
              :key="index"
              :cols="listName.flex"
            >
              <v-card @click="listClick(index)">
                <v-img
                  :src="require('../assets/list-img/' + listName.src)"
                  class="white--text align-end"
                  gradient="to bottom, rgba(0,0,0,.1), rgba(0,0,0,.5)"
                  height="200px"
                >
                  <v-card-title v-text="listName.title"></v-card-title>
                </v-img>
              </v-card>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
    </div>
  </v-container>
</template>

<script>
import * as CONFIG from "../config";
const google_myanmar_tools = require("myanmar-tools");
import HomeListComponent from "@/components/HomeList.vue";
export default {
  name: "HomeComponent",
  components: {
    HomeListComponent,
  },
  data: () => ({
    db: "",
    isDbEmpty: true,
    loading: false,
    isImportNotCompleted: false,
    dbInsertCount: 0,
    csvString: "",
    csvArr: [],
    csvArrCopy: [],
    csvArrFilter: [],
    isReadingFinish: false,
    searchValue: "",
    kotobaList: [
      { title: "JLPT N5", flex: 6, src: "5.jpg", level: 5 },
      { title: "JLPT N4", flex: 6, src: "4.jpg", level: 4 },
      { title: "JLPT N3", flex: 6, src: "3.jpg", level: 3 },
      { title: "JLPT N2", flex: 6, src: "2.jpg", level: 2 },
      { title: "JLPT N1", flex: 12, src: "1.jpg", level: 1 },
    ],
  }),
  created: function () {
    let that = this;
    async function displayList() {
      console.log("displayList");
      try {
        that.db = await CONFIG.openDb();
        console.log("openDb DONE", that.db);
      } catch (e) {
        console.log("error", e);
      }

      if (that.db) {
        var tx = that.db.transaction(CONFIG.DB_STORE_DICTIONARIES, "readwrite");
        var store = tx.objectStore(CONFIG.DB_STORE_DICTIONARIES);
        var req = store.count();
        req = store.openCursor();
        let dbCsvArr = [];
        req.onsuccess = async function (evt) {
          var cursor = evt.target.result;
          // console.log(cursor);
          // If the cursor is pointing at something, ask for the data
          if (cursor) {
            // console.log("displayList cursor:", cursor);
            req = store.get(cursor.key);
            req.onsuccess = function (evt) {
              var value = evt.target.result;
              // console.log("value", value.no);
              dbCsvArr.push(value);
              that.isDbEmpty = false;
            };

            // Move on to the next object in store
            cursor.continue();
          } else {
            console.log("No more entries", dbCsvArr.length);

            // import not successfuly yet, show warning then
            if (dbCsvArr.length < 10000) {
              console.log("warning");
              that.isImportNotCompleted = true;
            }

            that.isReadingFinish = true;
            that.csvArr = dbCsvArr;
          }
        };
        req.onerror = function () {
          console.error("add error", this.error);
        };
      }
    }
    displayList();
  },
  methods: {
    start: async function () {
      console.log("start");
      let that = this;

      var fileLoaderObj = document.getElementById("fileLoader");
      if (!fileLoaderObj.files.length) return;
      let file = fileLoaderObj.files[0];

      function getObjectStore(store_name, mode) {
        var tx = that.db.transaction(store_name, mode);
        return tx.objectStore(store_name);
      }

      function saveCsv(csvArr) {
        that.loading = true;
        csvArr.forEach(function (element, index) {
          let obj = {
            no: parseInt(element[0]),
            romaji: element[1],
            kana: element[2],
            kanji: element[3],
            meaning_mm: element[4],
            meaning_en: element[5],
            is_tran: element[6],
            level: element[7],
            part_of_speech: element[8],
            example_sentence_jp: element[9],
            example_sentence_en: element[10],
          };
          // console.log(obj.no);

          var store = getObjectStore(CONFIG.DB_STORE_DICTIONARIES, "readwrite");
          var req;
          try {
            req = store.add(obj);
          } catch (e) {
            if (e.name == "DataCloneError")
              console.log(
                "This engine doesn't know how to clone a Blob, " + "use Firefox"
              );
            throw e;
          }
          req.onsuccess = function () {
            console.log("Insertion in DB successful");
            that.dbInsertCount++;
            if (index == csvArr.length - 1) {
              that.loading = false;
              that.isDbEmpty = false;
              location.reload();
            }
          };
          req.onerror = function () {
            console.error("saveCsv error", this.error);
          };
        });
      }

      function convertCSVToJSON(str) {
        const rows = str.slice(str.indexOf("\n") + 1).split("\n");
        return rows.map((row) => row.split(/,(?=(?:(?:[^"]*"){2})*[^"]*$)/));
      }

      function readFile() {
        const reader = new FileReader();
        reader.onload = function (e) {
          that.csvString = e.target.result;
          that.csvArr = convertCSVToJSON(that.csvString);
          console.log("csvArr", that.csvArr);
          if (that.csvArr.length > 0) {
            if (that.csvArr[0].length != 11) {
              alert("Wrong CSV file");
              return;
            } else {
              saveCsv(that.csvArr);
            }
          }
        };
        reader.readAsText(file);
      }

      if (that.db) {
        readFile();
      }
    },
    search: async function () {
      let that = this;
      let searchValue = that.searchValue.replace(/\s/g, "");
      console.log("search", searchValue);

      if (searchValue) {
        const detector = new google_myanmar_tools.ZawgyiDetector();
        const score = detector.getZawgyiProbability(searchValue);

        // detect search value is En, Mm, No.
        let isSearchValueEng = searchValue.match(/^[a-zA-Z¥-]*$/) != null;
        let isSearchValueMyanmar = score !== -Infinity;
        let isSearchValueNo = searchValue.match(/^[0-9０-９]*$/) != null;

        // အသုံးပြုသူတွေထဲမှာ Unicode ဖြစ်နိုင်ခြေများတယ်လို့ယူဆလို့ 0.9 သုံး၊ မယူဆရင် 0.05
        if (score === -Infinity) {
          console.log("မြန်မာစာမဟုတ်နိုင်");
          searchValue = searchValue.toLowerCase();
        } else if (score > 0.9) {
          console.log("Zawgyiဖြစ်နိုင်");
          const converter = new google_myanmar_tools.ZawgyiConverter();
          searchValue = converter.zawgyiToUnicode(searchValue);
        } else {
          console.log("Unicodeဖြစ်နိုင်");
        }

        if (searchValue == "import-again") {
          // have to empty the database
          await CONFIG.clearDictionariesStore(that.db);
          console.log("clearDictionariesStore done");
          that.isDbEmpty = true;
          return;
        }

        if (!that.csvArrCopy.length) {
          that.csvArrCopy = that.csvArr;
        }
        that.csvArr = that.csvArrCopy.filter(
          (e) =>
            e.kanji.indexOf(searchValue) > -1 ||
            e.kana.indexOf(searchValue) > -1 ||
            e.no.toString().indexOf(searchValue) > -1 ||
            e.meaning_mm.indexOf(searchValue) > -1 ||
            e.meaning_en.toLowerCase().indexOf(searchValue) > -1
        );
        console.log("that.csvArr", that.csvArr);

        // resort the result for helping the finding word
        if (isSearchValueNo) {
          that.csvArrFilter = that.csvArr.sort(
            (kotobaA, kotobaB) =>
              searchValue.indexOf(kotobaB.no) - searchValue.indexOf(kotobaA.no)
          );
        } else if (isSearchValueMyanmar) {
          that.csvArrFilter = that.csvArr.sort(
            (kotobaA, kotobaB) =>
              searchValue.indexOf(kotobaB.meaning_mm) -
              searchValue.indexOf(kotobaA.meaning_mm)
          );
        } else if (!isSearchValueEng) {
          that.csvArrFilter = that.csvArr.sort(
            (kotobaA, kotobaB) =>
              searchValue.indexOf(kotobaB.kanji) -
              searchValue.indexOf(kotobaA.kanji)
          );
          that.csvArrFilter = that.csvArr.sort(
            (kotobaA, kotobaB) =>
              searchValue.indexOf(kotobaB.kana) -
              searchValue.indexOf(kotobaA.kana)
          );
        } else {
          that.csvArrFilter = that.csvArr;
        }
      }
    },
    searchKeyCheck: function () {
      if (!this.searchValue) {
        this.csvArrFilter = [];
      }
    },
    goto: function (location) {
      this.$router.push(location);
    },
    searchClear: function () {
      this.searchValue = "";
    },
    onChildClick: function (value) {
      console.log("onChildClick", value);
      this.$emit("childToParent", value);
    },
    listClick: function (index) {
      console.log("listClick", this.kotobaList[index]);
      let that = this;
      if (!that.csvArrCopy.length) {
        that.csvArrCopy = that.csvArr;
      }
      that.csvArrFilter = that.csvArrCopy.filter(
        (e) => CONFIG.getLevel(e.level, e.no) == this.kotobaList[index].level
      );
      that.searchValue = "abc";
      console.log("listClick finish");
    },
  },
};
</script>

<style>
.search {
  padding-bottom: 1rem;
}
.v-input input {
  line-height: 1rem !important;
}

.kotoba-list {
  margin-top: 6px;
}

.kotoba-list span {
  font-size: 14px;
}
</style>