<template>
  <v-card class="mx-auto search-result">
    <div v-for="(csv, index) in csvArrFilter" v-bind:key="index">
      <v-lazy
        v-model="csv.no"
        :options="{
          threshold: 0.5,
        }"
        class="fill-height"
      >
        <v-list-item two-line>
          <v-list-item-content @click="detail(csv['no'].toString())">
            <v-list-item-title>
              <span class="kanji"
                >{{ csv["kanji"] ? csv["kanji"] : csv["kana"] }}
              </span>
              <span class="kana">{{ csv["kana"] }}</span>
            </v-list-item-title>
            <v-list-item-subtitle class="myanmar meaning">
              {{ csv["meaning_mm"] ? csv["meaning_mm"] : csv["meaning_en"] }}
            </v-list-item-subtitle>
          </v-list-item-content>
          <v-list-item-action class="search-result-action">
            <span class="level">{{ getLevel(csv["level"], csv["no"]) }}</span>
            <v-btn icon @click="favoriteToggle(csv['no'])">
              <v-icon
                color="red lighten-1"
                v-if="favArr.indexOf(csv['no']) == -1"
                >mdi-heart-outline</v-icon
              >
              <v-icon color="red lighten-1" v-else>mdi-heart</v-icon>
            </v-btn>
            <span class="no">{{ csv["no"] }}</span>
          </v-list-item-action>
        </v-list-item>
      </v-lazy>
    </div>
  </v-card>
</template>

<script>
import * as CONFIG from "../config";
export default {
  name: "HomeListComponent",
  props: {
    csvArrFilter: Array,
  },
  data: () => ({
    favArr: [],
  }),
  methods: {
    detail: function (no) {
      this.$emit("childToParent", no);
    },
    favoriteToggle: function (no) {
      let foundIndex = this.favArr.indexOf(no);
      if (foundIndex == -1) {
        this.favArr.push(no);
      } else {
        this.favArr.splice(foundIndex, 1);
      }
      console.log("favoriteToggle", no);
    },
    getLevel: function (level, no) {
      return CONFIG.getLevel(level, no);
    },
  },
};
</script>