<template>
  <div class="home">
    <HomeComponent v-on:childToParent="onChildClick" v-show="!no" />
    <DetailComponent ref="parentToChildRef" v-show="no" />
  </div>
</template>

<script>
// @ is an alias to /src
import HomeComponent from "@/components/Home.vue";
import DetailComponent from "@/components/Detail.vue";

export default {
  name: "Home",
  components: {
    HomeComponent,
    DetailComponent
  },
  data: () => ({
    no: "",
  }),
  created: function () {
    let that = this;
    window.onpopstate = function (event) {
      console.log(
        "location: " +
          document.location +
          ", state: " +
          JSON.stringify(event.state)
      );
      if (!event.state || event.state.key) {
        console.log("no null");
        that.no = "";
      } else if (event.state.no) {
        console.log("no set again");
        that.onChildClick(event.state.no);
      }
    };
  },
  methods: {
    // Triggered when `childToParent` event is emitted by the child.
    onChildClick(value) {
      console.log("onChildClick", value);

      if (value) {
        // change url for detail
        window.history.pushState({ no: value }, "", "#/detail/" + value);

        this.no = value;
        this.$refs.parentToChildRef.displayDetail(value);
      }
    },
  },
};
</script>
